import App from './xpage/core';
// import {TweenLite} from "gsap";
import domReady from './xpage/ready';

import {Swiper, EffectFade, Autoplay, Pagination, Navigation, Lazy} from "swiper/js/swiper.esm";

Swiper.use([EffectFade, Autoplay, Pagination, Navigation, Lazy]);

console.log(12344);


domReady(async () => {
    let mainSliderh: Swiper;
    
    mainSliderh = new Swiper(".main-slider", {
        effect: "fade",
        slidesPerView: 1,
        simulateTouch: false,
        autoplay: {
            delay: 4000,
            stopOnLastSlide: true,
            disableOnInteraction: false
        },
        // autoplay: false,
        pagination: {
            el: ".main-slider__dots .swiper-pagination",
            type: "bullets",
            clickable: true
        },
        lazy: {
            loadPrevNextAmount: 3,
            loadOnTransitionStart: true,
        },
        on: {

            init() {


                const video = document.querySelector(".main-slider video") as HTMLVideoElement;

                if (!video)
                    return;

                video.addEventListener("ended", () => {
                    this?.autoplay?.start()
                });

                this?.autoplay?.stop();

                // if (window.is.desktop() && !window.is.safari())
                //     video.muted = false;
                // else
                document.querySelector(".main-slider")?.classList.add("js__show-sound_toggler");

                video.addEventListener("ended", () => {
                	this?.autoplay?.start();
                });

                if(document.querySelector('.swiper-slide--video.swiper-slide-active')){
                	this.autoplay.stop();
                	try{
                		video.play();
                		// this.slideTo(0);
                	}catch(e){
                		document.addEventListener("click",  video.play);
                	}
                }


                const toggler = document.querySelector(".sound-toggler") as HTMLButtonElement;

                if (!toggler) return;


                toggler.addEventListener("click", function () {
                    if (this.classList.contains("js__enabled")) {
                        video.muted = true;
                        // video.setAttribute("muted", "");
                        this.classList.remove("js__enabled");
                    } else {
                        video.muted = false;
                        // video.removeAttribute("muted");
                        this.classList.add("js__enabled");
                    }
                });


            },
            reachEnd() {

                const video = document.querySelector(".main-slider video") as HTMLVideoElement;

                if (!video)
                    return;

                let durationVideo = video.duration;


                setTimeout(() => {
                    this.autoplay.stop();
                    this.slideTo(0);

                    this.autoplay.start();

                }, Math.ceil(durationVideo) * 1000)


            },
            slideChangeTransitionStart() {
                // moveArrows();
                const video = document.querySelector(".main-slider video") as HTMLVideoElement;


                if (document.querySelector('.swiper-slide--video.swiper-slide-active')) {

                    let durationVideo = video.duration;

                    console.log(Math.ceil(durationVideo) * 1000);


                    this.autoplay.stop();

                    setTimeout(function(){
                    	this.slideTo(0);
                    	this.autoplay.start();

                    }, Math.ceil(durationVideo) * 1000)

                    try{
                    	video.play();


                    }catch(e){
                    	document.addEventListener("click",  video.play);
                    }
                }
            },
            slideChangeTransitionEnd() {
                if (this.slides[this.activeIndex].classList.contains('white_dots')) {
                    this.pagination?.el.classList.add('white')
                } else {
                    this.pagination?.el.classList.remove('white')
                }
            },

        }
    });
    

    // Перекрашивание точек пагиинации
    if (mainSliderh.slides.length > 0)
        if (mainSliderh.slides[mainSliderh.activeIndex].classList.contains('white_dots')) {
            mainSliderh.pagination?.el.classList.add('white')
        } else {
            mainSliderh.pagination?.el.classList.remove('white')
        }

    const moveInfoMerch = (selector: string) => {
        let title = document.querySelector(selector)?.querySelector('.swiper-slide-active .merch__slide')?.getAttribute('data-title'),
            price = document.querySelector(selector)?.querySelector('.swiper-slide-active .merch__slide')?.getAttribute('data-price'),
            link = document.querySelector(selector)?.querySelector('.swiper-slide-active .merch__slide')?.getAttribute('data-link');


        document.querySelector('.merch__title').innerHTML = title || "";
        document.querySelector('.merch__price').innerHTML = price || "";
        document.querySelector('.merch__btn a')?.setAttribute('href', link || "");

        document.querySelector(selector)?.querySelectorAll('.swiper-slide').forEach((slide: HTMLElement) => {
            slide.classList.remove('js__animated');
        })

        setTimeout(() => {
            document.querySelector(selector)?.querySelector('.swiper-slide-active').classList.add('js__animated');
        }, 100);

    };


    new Swiper(".merch__slider .swiper-list", {
        effect: "fade",
        fadeEffect: {
            crossFade: true
        },
        slidesPerView: 1,
        // initialSlide: 2,
        loop: true,
        navigation: {
            nextEl: ('.merch__slider .swiper-button-next'),
            prevEl: ('.merch__slider .swiper-button-prev'),
        },
        autoplay: {
            delay: 4000,
            disableOnInteraction: false
        },
        // pagination: {
        //     el: '.swiper-pagination',
        //     clickable: true,
        //     renderBullet: function (index, className) {
        //         return '<span class="' + className + '">' + (index + 1) + '</span>';
        //         },

        // },

        on: {
            init() {
                moveInfoMerch(".merch__slider .swiper-list");
            },
            reachEnd() {

            },
            slideChangeTransitionEnd() {
                moveInfoMerch(".merch__slider .swiper-list");
            },
            touchMove() {
                moveInfoMerch(".merch__slider .swiper-list");

            },
        },
    })


});

document.addEventListener("DOMContentLoaded", () => {
    setTimeout(function () {
        document.querySelector('body').classList.remove('loading');
        document.querySelector('body').classList.add('loaded');

    }, 300)
})


// window.addEventListener("load", function(){
document.addEventListener("DOMContentLoaded", function () {
    const options = {
        rootMargin: "0px",
        thresold: .7
    };

    function callback(entries: Array<IntersectionObserverEntry>, observer: IntersectionObserver) {
        entries.map((entry: IntersectionObserverEntry, i: number) => {
            if (entry.isIntersecting) {
                entry.target.classList.add("js__active");
            }
        })
    };

    const obs = new IntersectionObserver(callback, options);

    // obs.observe(document.querySelector(".results"))

    App.each(".results, .main .footer, .main-blockquote, .sliders-tab, .merch, .main-banner, .player-characteristics, .main-news, .first-screen, .stats-list__row, .counters__counter, .statistic__item, .main-team, .merch-top, .voting, .voting-statistic__progress, .fadeInLeft", function (el: HTMLElement) {
        obs.observe(el);

    })


})
